export const initialState = {
    user: null,
    fuser:null
}
export const actionTypes = {
    SET__USER:"SET__USER"
}
 const reducer=(state, action) => {
     console.log("this the action" + action)
     console.log("this usre " + action.fuser)
    switch(action.type){
        case actionTypes.SET__USER:
            return {
                ...state,
                user:action.user,
            }
                case actionTypes.SET__USER:
                    return {
                        ...state,
                        fuser:action.fuser,
                    }
            default:
                return state;
    }
};
export default reducer;