import  Axios  from './Axios'
import React, { useEffect, useState } from 'react'
import { useStateValue } from './StateProvider'
import './UserAdd.css'
import { db } from './Firebase'

function UserAdd({dbfuser}) {
    const [{user,fuser},dispatch]=useStateValue()
    const [l, setl] = useState(0)
    const [luser, setluser] = useState([])
    const [clicedNew, setclicedNew] = useState(null)
    useEffect(() => {
        db.collection('AllUser').onSnapshot((snapShot) => {
            setluser(snapShot.docs.map(doc => doc.data().displayName))
        })
          for(var a=0;a<=dbfuser.length;a++){
            setl(a)
          }
        
        }, [])
const CreateUser = () => {
    // const data = new FormData;
    // data.append("avater","noenoe")
    // data.append("UserName","mahfuz")
    // Axios.post('/allUsers',{
    //     data
    // })
    if(user){
        if(!(user.displayName===luser[l])){
            db.collection("AllUser").add({
                photoURL:user.photoURL,
                displayName:user.displayName,
                message:"created"
            })
            alert('success')
        }
    
    }
    setclicedNew("clicked")
}

    return (
        <div className="userAdding__main__container">
            <div className="user__field">
                {/* <div><img src="" /></div> */}
                <div className="information__setup">
                    <div className="user__select__img"><img src={user?.photoURL} /></div>
                    <div className="usre__input__container"><input type="text" value={user?.displayName} placeholder="user Name"/></div>
                    <div className="information__information__Button">

                        {clicedNew?(<div className="information__Button">You just clicked</div>):
                        (<div onClick={CreateUser} className="information__Button">Create a Profile</div>)}
                        </div>
                </div>
            </div>
        </div>
    )
}

export default UserAdd
