import { Avatar } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { db } from './Firebase'
import './FollowerSuggests.css'
import Fperson from './Fperson'

function FollowerSuggests() {
    const [folowerUser, setfolowerUser] = useState([])


    useEffect(() => {
        db.collection('AllUser').onSnapshot((snapShot) => {
         setfolowerUser(snapShot.docs.map(doc => doc.data()))
        })
      
        }, [])
    return (
        <div className="home__container__right__content">
            <div className="suggest__top" ><div><a>add to your feed</a></div><div>i</div> </div>

            {folowerUser.map(follower=>(
                        <Fperson fUrl={follower.photoURL} fName={follower.displayName} />
                   ))}
    </div>
    )
}

export default FollowerSuggests
