import { useEffect, useState } from 'react';
import './App.css';
import Body from './Body.js';
import Header from './Header';
import LoginandReg from './LoginandReg.js'
import { useStateValue } from './StateProvider';
import Axios from './Axios.js';
import UserAdd from './UserAdd';
import { db } from './Firebase';
import { actionTypes } from './reducer'

function App() {
  const [{user,fuser},dispatch] = useStateValue()
  const [userName, setuserName] = useState(null)
  const [userProfile, setuserProfile] = useState(null)
  const [dbfuser, setdbfuser] = useState([])
  const [i, seti] = useState(0)

useEffect(() => {
  db.collection('AllUser').onSnapshot((snapShot) => {
    setdbfuser(snapShot.docs.map(doc => doc.data().displayName))
  })
  if(dbfuser){
    dispatch({
      type:actionTypes.SET__USER,
      fuser:dbfuser,
    })
    console.log('dispatch successful')
    console.log(dbfuser)
    for(var a=0;a<=dbfuser.length;a++){
      seti(a)
    }
  }
  else{
    console.log('dispatch failed')
  }

  }, [])
  console.log(dbfuser)
  console.log(fuser)
  return (
    <div className="App">
      <div className="linkdin">
{user?           
      ( <> <Header />
             {(dbfuser)? (<Body />):
              (
                <>
              {(user.displayName===dbfuser[i])?
                (<UserAdd dbfuser={dbfuser} />):
                (<></>)
              }
              </>
              )
              }
              </>)   :
        (<LoginandReg />)
        }
      </div>
    </div>
  );
}

export default App;
