import { Avatar } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { db } from './Firebase'
import "./Fperson.css"

function Fperson({fUrl,fName}) {
    const [singlef, setsinglef] = useState(null)
    const [i, seti] = useState(0)
const Follow = () => {
    if(fUrl&&fName){
        db.collection('AllUser')
        .doc("PJPfeGkwBJqknnq1osvp").collection('followers').add({
           photoURL: fUrl,
           displayName:fName,
        })
        alert("following")
    }
}

useEffect(() => {
    db.collection('AllUser')
    .doc("PJPfeGkwBJqknnq1osvp").collection("followers").onSnapshot((shot) => {
        // setfollower(shot.docs.map(doc => doc.data()))
        // console.log("follower__data>>>" + shot.docs.map(doc => doc.data().displayName))
        const data = shot.docs.map(doc => doc.data())
        if(data){
            for(var a=0;a<data.length;a++){
            setsinglef(data[a].displayName)
            console.log("form " + data[a].displayName)
            }
        }
    })


  
    }, [])
    return (
        <div className="home__container__right__follower">
            <div><Avatar src={fUrl} /></div>
            <div>
                <div><a>{fName}</a></div>
                <div><a>this about </a></div>
{(fName===singlef)?
               (<div className="follow followed">Message</div>) :
                (<div className="follow" onClick={Follow}>+Follow</div>)}
            </div>
       </div>
    )
}

export default Fperson
