import React, { useState } from 'react'
import SearchIcon from '@mui/icons-material/Search';
import HomeIcon from '@mui/icons-material/Home';
import SmsOutlinedIcon from '@mui/icons-material/SmsOutlined';
import PeopleAltOutlinedIcon from '@mui/icons-material/PeopleAltOutlined';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import GroupIcon from '@mui/icons-material/Group';
import WorkIcon from '@mui/icons-material/Work';
import SmsIcon from '@mui/icons-material/Sms';
import WorkOutlineOutlinedIcon from '@mui/icons-material/WorkOutlineOutlined';
import NotificationsNoneIcon from '@mui/icons-material/NotificationsNone';
import NotificationsIcon from '@mui/icons-material/Notifications';
import './Header.css'
import { Avatar, IconButton } from '@mui/material';
import { useStateValue } from './StateProvider';
import { auth } from 'firebase';
import { actionTypes } from './reducer';

function Header() {
  const [{user},dispatch] = useStateValue()
  const [home, sethome] = useState("clicked")
  const [PeopleAltOutlined, setPeopleAltOutlined] = useState(null)
  const [SmsOutlined, setSmsOutlined] = useState(null)
  const [NotificationsNone, setNotificationsNone] = useState(null)
  const [WorkOutlineOutlined, setWorkOutlineOutlined] = useState(null)


   const logOut = () => {
    auth().signOut().then(function() {
        // Sign-out successful.
        dispatch({
            type:actionTypes.SET__USER,
            user:null,
        })
      }).catch(function(error) {
        // An error happened.
      })
  }
  const homeClick = () => {
    setPeopleAltOutlined(null)
    setSmsOutlined(null)
    setNotificationsNone(null)
    setWorkOutlineOutlined(null)
    sethome('clicked')

  }
  const peopleClick = () => {
    sethome(null)
    setSmsOutlined(null)
    setNotificationsNone(null)
    setWorkOutlineOutlined(null)
    setPeopleAltOutlined("clicked")

  }
  const smsClicked = () => {
    setPeopleAltOutlined(null)
    sethome(null)
    setWorkOutlineOutlined(null)
    setNotificationsNone(null)
    setSmsOutlined("clicked")
  }
  const NotClicked = () => {
    setPeopleAltOutlined(null)
    sethome(null)
    setSmsOutlined(null)
    setWorkOutlineOutlined(null)
    setNotificationsNone("clicked")
  }
  const jobClicked = () => {
    setPeopleAltOutlined(null)
    sethome(null)
    setSmsOutlined(null)
    setNotificationsNone(null) 
    setWorkOutlineOutlined("clicked")   
  }
    return (
        <div className="header__main__container">
            <div className="header__logo__main">
                <div className="header__logo__main__img"><img src="data:image/png;base64," /></div>
                <div className="header__logo__main__search__bar">
                    <div><SearchIcon /><input className="header__logo__main__search__bar__input" type="text" placeholder="Search here..."/>
                    </div>
                </div>
            </div>
            <div className="header__icon__main">
 {home?          (<div class="active">
                    <HomeIcon />
                    <div className="hide "><p>Home</p></div>
                </div>):
                (<div onClick={homeClick}>
                    <HomeOutlinedIcon />
                    <div className="hide"><p>Home</p></div>
                </div>)}
{PeopleAltOutlined? (<div class="active">
                    <GroupIcon />
                    <div className="hide"><p>Network</p></div>
                </div>):
                (<div  onClick={peopleClick}>
                    <PeopleAltOutlinedIcon />
                    <div className="hide"><p>Network</p></div>
                </div>)}
{SmsOutlined?  (<div class="active">
                    <SmsIcon />
                    <div className="hide"><p>Message</p></div>
                </div>):
                (<div onClick={smsClicked}>
                    <SmsOutlinedIcon />
                    <div className="hide"><p>Message</p></div>
                </div>)}
 {NotificationsNone? (<div class="active">
                    <NotificationsIcon  />
                    <div className="hide"><p>Notification</p></div>
                </div>):
                (<div  onClick={NotClicked}>
                    <NotificationsNoneIcon  />
                    <div className="hide"><p>Notification</p></div>
                </div>)}
{WorkOutlineOutlined? (<div class="active">
                    <WorkIcon />
                    <div className="hide"><p>Job</p></div>
                </div>):
                (<div onClick={jobClicked}>
                    <WorkOutlineOutlinedIcon />
                    <div className="hide"><p>Job</p></div>
                </div>)}
                <div>
                    <IconButton onClick={logOut}>
                     <Avatar sx={{ width: 30, height: 30 }} src={user?.photoURL} />
                    </IconButton>
                </div>
            </div>
        </div>
    )
}

export default Header
